<script>
import PostsService from "@/services/apiService";
import VueInstantLoadingSpinner from "vue-instant-loading-spinner/src/components/VueInstantLoadingSpinner.vue";

export default {
  name: "response",
  data() {
    return {
        voterId: "",
        pwd: "",
        errors: [],
        successModal: false,
        errorModal: false,
        loginResponse: {
        data: ""
        },
        rules: {
            required: value => !!value || "입력해주세요",
            counter: value => value.length <= 20 || "Max 20 characters",
            voterId: value => {
            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return pattern.test(value) || "아이디를 입력해주세요";
            },
            pwd: value => {
            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return pattern.test(value) || "비밀번호를 입력해주세요";
            }
        }



    //   loginData: {},
    //   registerData: {},
    //   registerReponse: {
    //     data: ""
    //   },
    //   loginReponse: {
    //     data: ""
    //   }
    };
  },
  components: {
    VueInstantLoadingSpinner
  },
  methods: {
    async registerVoter() {

      await this.runSpinner();
      const apiResponse = await PostsService.registerVoter(
        this.registerData.voterId,
        this.registerData.registrarId,
        this.registerData.firstName,
        this.registerData.lastName
      );

      console.log(apiResponse);
      this.registerReponse = apiResponse;
      await this.hideSpinner();
    },

    async onSignUp() {
      this.$router.push("signUp");
    },

    async onSubmit() {
        if(!this.voterId) {
            console.log("!thisVoterId");
            let response = 'Please enter a VoterId';
            this.loginResponse.data = response;

        }
        else if(!this.pwd){
            console.log("!thisPWD");
            let response = "Please enter a PASSWORD";
            this.loginResponse.data = response;
        }
        else {
            const apiResponse = await PostsService.loginVoter(
                this.voterId,
                this.pwd
            );
            console.log("apiResponse");
            console.log(apiResponse.data);

            if(apiResponse.data.error){
                console.log(apiResponse.data.error);
                this.loginResponse - apiResponse.data.error;
            }
            else {
                this.$router.push("castBallot");
            }
             console.log(apiResponse);
            this.loginReponse = apiResponse;
        }
    },

    async validateVoter() {
      await this.runSpinner();

      if (!this.loginData.voterId) {
        console.log("!thislogin");
        let response = 'Please enter a VoterId';
        this.loginReponse.data = response;
        await this.hideSpinner();
      } else {
        const apiResponse = await PostsService.validateVoter(
          this.loginData.voterId
        );
        console.log("apiResponse");
        console.log(apiResponse.data);

        if (apiResponse.data.error) {
          // console.log(apiResponse);
          console.log(apiResponse.data.error);
          this.loginReponse = apiResponse.data.error;
        } else {
          this.$router.push("castBallot");
        }

        console.log(apiResponse);
        this.loginReponse = apiResponse;
        // this.$router.push('castBallot')
        await this.hideSpinner();
      }
    },
    async runSpinner() {
      this.$refs.Spinner.show();
    },
    async hideSpinner() {
      this.$refs.Spinner.hide();
    }
  }
};
</script>


